body, html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  width: 100%;
  height: 100%;
}

h1{
  text-align: center;
  font-size: 24px;
}

h1.blink{
  -webkit-animation: blink 1s 1;
          animation: blink 1s 1;
}

.controler{
  width: 300px;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.controler button {
  width: 145px;
  border: none;
  border-radius: 30px;
  height: 40px;
  color: white;
  background: #ccc;
  line-height: 40px;
  font-size: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
  margin: 0;
  -webkit-tap-highlight-color: transparent;
  outline: none;
}

.controler button:active{
  -webkit-box-shadow: inset 20px 20px 10px rgba(255,255,255,.2);
          box-shadow: inset 20px 20px 10px rgba(255,255,255,.2);
}

button.like{
  background-color: #fb7299;
  margin-right: 10px;
}

@-webkit-keyframes blink {
  0%, 50%{
    opacity: 0;
  }
  25%, 100%{
    opacity: 1;
  }
}

@keyframes blink {
  0%, 50%{
    opacity: 0;
  }
  25%, 100%{
    opacity: 1;
  }
}


